<script setup>
import Auth from "@/api/Auth";
import { ref, reactive } from "vue";

const rules = (value) => !!value || 'Kolom harus diisi.'
const show = ref(null)
const message = ref()

const form = reactive({
    username: '',
    password: ''
})


async function login() {
    if (form.username && form.password) {
        await Auth.login(form)
            .then(async () => {
                location.reload()
            })
            .catch((err) => {
                message.value = err.response.data.message
            })
    }
}

</script>

<template>
    <h2>Login</h2>

    <v-divider class="my-4"></v-divider>

    <v-form @submit.prevent="login" class="px-6 py-4 border">
        <v-text-field v-model="form.username" class="mt-2" label="Username" prepend-icon="mdi-account" :rules="[rules]"
            variant="underlined"></v-text-field>
        <v-text-field v-model="form.password" variant="underlined" prepend-icon="mdi-lock" :rules="[rules]"
            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" :type="show ? 'text' : 'password'" label="Password"
            class="input-group--focused mt-2" @click:append="show = !show"></v-text-field>

        <v-btn type="submit" color="green" class="mt-4">Masuk</v-btn>

        <div class="mt-4">
            <strong class="text-red">{{ message }}</strong>
        </div>
    </v-form>
</template>