import { defineStore } from "pinia";
import Auth from "@/api/Auth";

export const useAuthStore = defineStore('auth', {
    state: () => ({
        authUser: null
    }),
    getters: {
        user: (state) => state.authUser
    },
    actions: {
        async getUser() {

            if (!this.authUser) {
                const res = await Auth.find()
                    .catch((err) => err)
                this.authUser = res
            }
        }
    }
})