import { useRoute } from "vue-router";
import Auth from "./api/Auth";
import Footnote from "./api/Footnote";

const drawer = ref(true)
const route = useRoute()

const user = ref()

const footnote = ref()


function activeButton(path) {
  return path.includes(route.name) ? 'flat' : 'outlined'
}

async function logout() {
  if (confirm('Keluar sesi ini?')) {
    await Auth.logout()
    location.reload()
  }
}

onBeforeMount(async () => {
  const res = await Auth.find().catch((err) => err)
  user.value = res?.username

  const note = await Footnote.all()
  footnote.value = note[0]
})

</script>

