import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import LoginView from '../views/LoginView.vue';
import { useAuthStore } from "@/stores/auth";

const routes = [

  // Auth
  {
    path: '/login',
    name: 'login',
    component: LoginView,
    meta: {
      auth: 'guard',
      title: 'Login'
    }
  },


  // Home
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      auth: true
    }
  },

  // About
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/About/AboutView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/edit-about',
    name: 'edit-about',
    component: () => import('../views/About/EditAboutView.vue'),
    meta: {
      auth: true
    }
  },


  // Infaq
  {
    path: '/infaq',
    name: 'infaq',
    component: () => import('../views/Infaq/InfaqView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/find-infaq/:id',
    name: 'find-infaq',
    component: () => import('../views/Infaq/FindInfaqView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/add-infaq',
    name: 'add-infaq',
    component: () => import('../views/Infaq/AddInfaqView.vue'),
    meta: {
      auth: true
    }
  },
  {
    path: '/edit-infaq/:id',
    name: 'edit-infaq',
    component: () => import('../views/Infaq/EditInfaqView.vue'),
    meta: {
      auth: true
    }
  },

  // Berita
  {
    path: '/berita',
    name: 'berita',
    component: () => import('../views/Berita/BeritaView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/find-berita/:id',
    name: 'find-berita',
    component: () => import('../views/Berita/FindBeritaView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/add-berita',
    name: 'add-berita',
    component: () => import('../views/Berita/AddBeritaView.vue'),
    meta: {
      auth: true
    }
  },
  {
    path: '/edit-berita/:id',
    name: 'edit-berita',
    component: () => import('../views/Berita/EditBeritaView.vue'),
    meta: {
      auth: true
    }
  },


  // Laporan
  {
    path: '/laporan',
    name: 'laporan',
    component: () => import('../views/Laporan/LaporanView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/find-laporan/:id',
    name: 'find-laporan',
    component: () => import('../views/Laporan/FindLaporanView.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/add-laporan',
    name: 'add-laporan',
    component: () => import('../views/Laporan/AddLaporanView.vue'),
    meta: {
      auth: true
    }
  },
  {
    path: '/edit-laporan/:id',
    name: 'edit-laporan',
    component: () => import('../views/Laporan/EditLaporanView.vue'),
    meta: {
      auth: true
    }
  },

  // Footnote
  {
    path: '/footnote',
    name: 'footnote',
    component: () => import('../views/Footnote/FootnoteView.vue'),
    meta: {
      auth: true
    }
  },

  // User
  {
    path: '/user',
    name: 'user',
    component: () => import('../views/UserView.vue'),
    meta: {
      auth: true
    }
  },


  // Payment
  {
    path: '/payment/:id',
    name: 'payment',
    component: () => import('../views/PaymentView.vue'),
    meta: {
      auth: false
    }
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// Middllware
router.beforeEach(async (to, from, next) => {

  const authStore = useAuthStore()
  await authStore.getUser()

  if (authStore.user) {
    if (to.meta.auth === 'guard') {
      next(from)
    } else {
      next()
    }
  } else {
    if (to.meta.auth === true) {
      next('/infaq')
    } else {
      next()
    }
  }

})

export default router
